  .side-card {
    position: relative;
    width: 100%;
    margin: auto;
    overflow: hidden;
  }
  
  .side-card .side-card-overlay {
    background: rgba(0,0,0,0.7);
    position: absolute;
    height: 99%;
    width: 100%;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    opacity: 0;
    transition: all 0.4s ease-in-out 0s;
    cursor: pointer;
  }


  
  .side-card:hover .side-card-overlay{
    opacity: 1;
  }
  
  .side-card-image{
    width: 70%;
  }
  
  .side-card-details {
    position: absolute;
    text-align: center;
    padding-left: 1em;
    padding-right: 1em;
    width: 100%;
    top: 50%;
    left: 50%;
    opacity: 0;
    transform: translate(-50%, -50%);
    transition: all 0.3s ease-in-out 0s;
  }
  
  .side-card:hover .side-card-details{
    top: 50%;
    left: 50%;
    opacity: 1;
  }
  
  .side-card-details h3{
    color: #fff;
    font-weight: 500;
    letter-spacing: 0.15em;
    margin-bottom: 0.5em;
    text-transform: uppercase;
  }
  
  .side-card-details p{
    color: #fff;
    font-size: 0.8em;
  }
  


