/* ------------------------------------------------------------------ */
/* h. Contact Section
/* ------------------------------------------------------------------ */

#contact {
  color: #cfcfcf;
}


#contact h4 {
  font: 16px/24px 'opensans-bold', sans-serif;
  margin-bottom: 6px;
}
#contact p.lead {
  font: 18px/36px 'opensans-light', sans-serif;
  padding-right: 3%;
}
#contact .header-col { padding-top: 6px; }

#contact .address {
  padding-top: -10px;
}

/* contact form */
#contact form { margin-bottom: 30px; }
#contact label {
  font: 15px/24px 'opensans-bold', sans-serif;
  margin: 12px 0;
  color: #bbb9b9;
 display: inline-block;
 /* float: left; */
  width: 26%;
}
#contact input,
#contact textarea,
#contact select {
  padding: 10px 5px;
 /* color: rgb(0, 0, 0); */
 background: #beb9ba;
 margin-bottom: 10px;
 border: 0;
 outline: none;
  font-size: 12px;
  line-height: 18px;
  width: 65%;
}
#contact input:focus,
#contact textarea:focus,
#contact select:focus {
 /* color: rgb(0, 0, 0); */
 background-color: white;
}
#contact button.submit {
 text-transform: uppercase;
 letter-spacing: 3px;
 color:rgb(255, 255, 255);
  background: #0D0D0D;
  border: none;
  cursor: pointer;
  height: auto;
  display: inline-block;
 border-radius: 0px;
  margin-left: 26%;
}
#contact button.submit:hover {
 color: #ffffff;
 background: rgb(1, 92, 28);
}
#contact span.required {
 color: #0762f9;
 font-size: 13px;
}
#message-warning, #message-success {
  display: none;
 background: #0F0F0F;
 padding: 24px 24px;
 margin-bottom: 36px;
  width: 65%;
  margin-left: 26%;
}
#message-warning { color: #D72828; }
#message-success { color: #0762f9; }

#message-warning i,
#message-success i {
  margin-right: 10px;
}
#image-loader {
  display: none;
  position: relative;
  left: 18px;
  top: 17px;
}

