.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  justify-content: start;
  margin-right: 1rem;
}

.nav-item {
  display: flex;
  align-items: center;
  height: 80px;
}

.Navbar{
  font-weight: 700;
  top: 0;
  width: 100%;
  height: 80px;
  display: flex;
  position: absolute;
  background-color: white;
  flex-direction: row;
}

.Navbar .leftside{
  flex: 62% 1;
  display: flex;
  justify-content: left;
  align-items: center;
  display: grid;
}

.Navbar .leftside .Links{
  max-height: 80px;
}

.Navbar .leftside .Links a{
  text-decoration: none;
  /* color: white; */
