  margin-left: 20px;
}

.Navbar .leftside #hidden{
  display: flex;
  /* margin-left: 20px; */
}

.Navbar .rightside{
  flex: 38% 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 25px;
}

.Navbar .rightside #navlogo{
  height: 80px;
  display:flex;
  padding-top: 10px;
}

