.dropdown-menu {
  background: transparent;
  /* width: 200px; */
  position: absolute;
  padding-top: 10px;
  top: 70px;
  list-style: none;
  text-align: start;
}

.dropdown-menu li {
  background: #419afa;
  cursor: pointer;
}

.dropdown-menu li:hover {
